<template>
    <div class="MemberClass">
      <HomePageTop :TopTitle="TopTotle" class="bg_fff"></HomePageTop>
      <div class="bg_fff pt30 mt25">
      <div class="swiper-container pb24 center_swiper" >
        <div class="swiper-wrapper">
          <div class="swiper-slide" v-for="(data,index) in VipHome" :key="index">
            <div  class="VipClass" :class="'vip'+index">
              <span>{{data.expression}}</span>
              <p> 距离下一等级还差:{{data.degree}}</p>
            </div>

          </div>

        </div>
      </div>
      <div class="prerogative both">
        <div class="MemberBox">
          <p class="f34 mb14 center">LV.{{SwipersIndex}}会员等级说明</p>
          <table  frame="border" rules="all" class="mb30">
            <tr><td>0-5㎡(测试费)</td><td>{{VipExplain[SwipersIndex].Home1}}</td></tr>
            <tr><td>5㎡以上(测试费)</td><td>{{VipExplain[SwipersIndex].Home2}}</td></tr>
            <tr><td>5㎡以上(测试架)</td><td>{{VipExplain[SwipersIndex].Home3}}</td></tr>
            <tr><td>销售</td><td>{{VipExplain[SwipersIndex].Home4}}</td></tr>
            <tr><td>客服</td><td>{{VipExplain[SwipersIndex].Home5}}</td></tr>
          </table>
          <p class="f28 cl_666">特别说明: <br><span class="cl_999">1.订单金额及订单面积根据账号近6个月内的数据计算 <br>2.会员等级每天更新 <br>3.最终解释权归属捷配所有<br></span></p>
          <br>
<!--          <p class="f28 cl_666">会员福利说明：<span class="cl_999">五钻用户：每月可领取2张提速券。<br>皇冠用户：每月可领取5张提速券；皇冠客户可申请开通白条功能 。</span></p>-->
        </div>
      </div>
      </div>
    </div>

</template>

<script>
    import Vue from "vue"
    import  Swiper from "swiper/swiper-bundle.min"
    import  'swiper/swiper-bundle.min.css'
    import HomePageTop from "../common/HomePageTop";
    export default {
        name: "MemberClass",
        components: {HomePageTop},
        data() {
            return {
              //像子组件传值
              TopTotle:{name:"会员等级",left:true},
              //swiper切换的下表
              SwipersIndex:0,
              //vip
              VipHome:[
                {expression:"注册未登录",degree:"下单4次或3200元"},
                {expression:"历史下过订单",degree:"下单4次或者3200元"},
                {expression:"金额>2万且面积>5㎡",degree:"下单4次或3200元"},
                {expression:"金额>5万且面积>20㎡",degree:"下单4次或者3200元"},
                {expression:"金额>10万且面积>200㎡",degree:"下单4次或者3200元"},
                {expression:"金额>10万且面积>200㎡",degree:"下单4次或者3200元"},
                {expression:"金额>50万且面积>700㎡",degree:"下单4次或者3200元"},
                {expression:"金额>70万且面积>1000㎡",degree:"恭喜您！升为黑卡会员！"},
              ],
              VipExplain:[
                {Home1:"25元/0.5㎡(测试费)",Home2:"20元/0.5㎡",Home3:"600元/款",Home4:"随机",Home5:"随机"},
                {Home1:"25元/0.5㎡(测试费)",Home2:"20元/0.5㎡",Home3:"600元/款",Home4:"销售顾问",Home5:"专业客服"},
                {Home1:"25元/0.5㎡(测试费)",Home2:"15元/0.5㎡",Home3:"500元/款",Home4:"销售顾问",Home5:"专业客服"},
                {Home1:"25元/0.5㎡(测试费)",Home2:"10元/0.5㎡",Home3:"500元/款",Home4:"大客户经理及销售顾问",Home5:"大客户金牌客服"},
                {Home1:"25元/0.5㎡(测试费)",Home2:"10元/0.5㎡",Home3:"400元/款",Home4:"大客户经理及销售顾问",Home5:"大客户金牌客服"},
                {Home1:"25元/0.5㎡(测试费)",Home2:"10元/0.5㎡",Home3:"400元/款",Home4:"大客户经理及销售顾问",Home5:"大客户金牌客服"},
                {Home1:"25元/0.5㎡(测试费)",Home2:"10元/0.5㎡",Home3:"400元/款",Home4:"大客户经理及销售顾问",Home5:"大客户金牌客服"},
                {Home1:"25元/0.5㎡(测试费)",Home2:"10元/0.5㎡",Home3:"400元/款",Home4:"大客户经理及销售顾问",Home5:"大客户金牌客服"},
              ]
            }
        },
        methods: {},
        mounted() {
        let _this=this;
        var center_swiper= new Swiper('.center_swiper', {
          slidesPerView: 1.15,
          centeredSlides: true,
          spaceBetween: 0,
          loop: false,
          fade:{crossFade:true},
          on:{
            slideChangeTransitionEnd: function(){
              console.log(this.activeIndex);//切换结束时，告诉我现在是第几个slide
              _this.SwipersIndex=this.activeIndex;
            },
          }
        });
      },
        created() {

        }
    }

</script>

<style scoped>
  .VipClass{
    height: 3.2rem;padding: 1.4rem  0.45rem 0.45rem 1.32rem;width: 100%;
  }
  .VipClass span{font-size: .34rem;color: #fff;margin-bottom: .2rem;display: inline-block}
  .VipClass p{font-size: .26rem;color: #fff}
  .vip0{background: url("../../assets/img/PersonalCenter/vip1.png") no-repeat center;background-size: cover}
  .vip1{background: url("../../assets/img/PersonalCenter/vip2.png") no-repeat center;background-size: cover}
  .vip2{background: url("../../assets/img/PersonalCenter/vip3.png") no-repeat center;background-size: cover}
  .vip3{background: url("../../assets/img/PersonalCenter/vip4.png") no-repeat center;background-size: cover}
  .vip4{background: url("../../assets/img/PersonalCenter/vip5.png") no-repeat center;background-size: cover}
  .vip5{background: url("../../assets/img/PersonalCenter/vip6.png") no-repeat center;background-size: cover}
  .vip6{background: url("../../assets/img/PersonalCenter/vip7.png") no-repeat center;background-size: cover}
  .vip7{background: url("../../assets/img/PersonalCenter/vip8.png") no-repeat center;background-size: cover}


  .icon_box{background: url("../../assets/img/PersonalCenter/Totop.png") no-repeat center;display: inline-block;width: .38rem;height: .38rem;vertical-align: middle;background-size: cover}
  .prerogative{padding-bottom: .3rem;margin-bottom: .6rem;margin-top: .3rem}
  body,html{background: #F4F6F9}
  table{width: 100%;border-top-width: thin;border-right-width: thin;border-bottom-width: thin;border-left-width: thin;border-top-style: solid;border-bottom-style: solid;border-left-style: solid;border-right-style: solid;border-collapse: collapse;border-color: grey;}
  table tr td:nth-child(1){width: 2.24rem;border-bottom: 1px solid #D1D3D8;border-right:1px solid #D1D3D8;font-size: .28rem;}
  table tr td:nth-child(2){border-bottom: 1px solid #D1D3D8;}
  table tr td{text-align: center}
  table tr{height: 1.02rem;line-height: 1.02rem;}
  table{border: 1px solid #d1d3d8;font-size: .28rem;color: #333333}
  .MemberBox{
    padding: 0 .3rem;
  }
  .box111{opacity: 0;height: 0;}
  .box222{transition: all .1s ease;opacity: 1;height: auto;}
  .swiper-container {width: 100%;height: 100%;}
  .swiper-slide {
    font-size: 0.28rem;
    box-shadow: 0px .1rem .1rem 0px rgba(0, 0, 0, 0.1);
    background-size: cover;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    transform: scale(0.9);
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    transition: 300ms;
  }
  .swiper-slide-active,.swiper-slide-duplicate-active{
    transform: scale(1);
  }
</style>



